var that;

export default {
	data() {
		return {
			productList: []
		}
	},

	onLoad() {
		that = this;
		that.getData();
	},

	methods: {
		getData() {
			that.$utils.http(that.$api.product_list, function(res) {
				that.productList = res;
			});
		},

		buy(product) {
			uni.navigateTo({
				url: '/pages/product/buy?sid=' + product.sid
			})
		}
	},
	filters:{
		dealMax:function(max){
			return Math.floor((max + Math.random()) * 10000) / 10000;
		}
	}
}
